.jumbotron {
  width: 100%;
  margin-top: 51px;
  padding: 10px;
  background-image: -webkit-gradient(linear,left bottom, left top,from(#18ABFB), to(#004fb7) );
  background-image: -webkit-linear-gradient(bottom,#18ABFB 0%, #004fb7 100% );
  background-image: -o-linear-gradient(bottom,#18ABFB 0%, #004fb7 100% );
  background-image: linear-gradient(to top,#18ABFB 0%, #004fb7 100% );
  color: white !important;
  border-radius: 0;
}

.jumbotron .sub-heading {
  color: white !important;
}

#private-container .jumbotron {
  background: transparent;
  margin-top: 0;
}




